const imageReels = document.querySelectorAll<HTMLLinkElement>('.image-reel')
let currentImageIndex = 0
imageReels.forEach(imageReel =>
{
    const dialog: HTMLDialogElement = imageReel.querySelector('dialog') as HTMLDialogElement
    const previousButton: HTMLButtonElement = dialog.querySelector('.previous-button') as HTMLButtonElement
    const nextButton: HTMLButtonElement = dialog.querySelector('.next-button') as HTMLButtonElement
    imageReel.addEventListener("click", dialog.showModal)
    
    previousButton.addEventListener("click", _ => goBackImage)
    nextButton.addEventListener("click", _ => goForwardImage)
    // Get all nested img elements
    const images = imageReel.querySelectorAll('img')
    
    
    dialog.addEventListener("keypress", event =>
    {
        //Left arrow key
        if(event.key == "ArrowLeft")
        {
            images[currentImageIndex].classList.add('hidden')
            goBackImage(images)
        }
        //Right arrow key
        else if(event.key == "ArrowRight")
        {
            goForwardImage(images)
        }
    })

  
    
    
    for (let i = 1; i < images.length; i++)
    {
        images[i].classList.add('hidden')
    }

    // Get all nested button elements

});


function goBackImage(images: NodeListOf<HTMLImageElement>)
{
    --currentImageIndex
    images[currentImageIndex].classList.add('hidden')

    if (currentImageIndex < 0)
        currentImageIndex = images.length - 1
}

function goForwardImage(images: NodeListOf<HTMLImageElement>)
{
    ++currentImageIndex
    images[currentImageIndex].classList.add('hidden')

    if (currentImageIndex >= images.length)
        currentImageIndex = 0
}


